export default {"name":"毛蟹茶","img":"\\wulongcha\\maoxiecha\\img_2019112210595499o4j1.png","desc":["毛蟹茶原产于安溪县大坪乡福美村大丘仑。无性系品种。其由来据《茶树品种志》(1979年出版，福建省农业科学院茶叶研究所编著，74页)载：“据萍州村张加协(1957年71岁)云：‘清光绪三十三年(1907)我外出买布，路过福美村大丘仑高响家，他说有一种茶，生长极为迅速，栽后二年即可采摘我遂顺便带回100多株，栽于自己茶园’。由于产量高，品质好，于是毛蟹就在萍州附近传开。"],"menus":[{"name":"毛蟹茶茶叶历史","content":[{"text":"始于唐末。当时韩林学士韩屋有诗云：“古崖觅芝叟，乡俗乐茶歌”。"},{"text":"明清时期，是安溪茶叶走向鼎盛的一个重要阶段。明代，安溪茶业生产的一个显著特点是饮茶、植茶、制茶广泛传遍至全县各地，并迅猛发展成为农村的一大产业。代安溪茶农树无性繁殖法的发现与创造。明代以前，中国茶树均采用种子直播的有性繁殖法，茶树容易变种。明崇祯十三年(1640年)前后，安溪茶农从茶树枝条压在土壤中能生根发芽得到启发，创造出茶树整株压条繁殖法。因而，使安溪成了中国茶树无性繁殖的发源地。"}]},{"name":"毛蟹茶茶叶特点","content":[{"text":"成品外形紧密、砂绿色，颗粒手感好、均匀，落入盘中分量感明显，体现铁观音“沉重如铁”的美誉;茶汤微青入口顺滑，口感醇厚有观音香。绝不掺杂品种茶。毛蟹植株灌木型，中叶类，中芽种。树姿半开展、分枝稠密;叶形椭圆，尖端突尖，叶片平展;叶色深绿，叶厚质脆，锯齿锐利;芽梢肥壮，茎粗节短，叶背白色茸毛多，开花多，基本不结实。育芽能力强，但持嫩性较差，发芽密而齐，成园较快。适应性广，抗逆性强，易于栽培，产量较高，适制\n        乌龙茶\n        ，为色种高级茶;制红、\n        绿茶\n        ，毫色显露，外形美观，品质俱佳。茶条紧结，梗圆形，头大尾尖，芽叶嫩，多白色茸毛，色泽褐黄绿，尚鲜润。茶汤青黄或金黄色。叶底叶张圆小，中部宽，头尾尖，锯齿深，密、锐、而且向下钩，叶稍薄，主脉稍浮现。味清纯略厚，香清高，略带茉莉花香。毛蟹植株灌木型，中叶类，中芽种。树姿半开展，分枝稠密;叶形椭圆，尖端突尖，叶片平展;叶色深绿，叶厚质脆，锯齿锐利;芽梢肥壮，茎粗节短，叶背白色茸毛多，开花尚多，但基本不结实。"},{"src":"\\wulongcha\\maoxiecha\\img_20191122105954xnklmv.jpg"}]},{"name":"毛蟹茶加工工艺","content":[{"text":"采摘"},{"text":"采摘是用食指与拇指挟住叶间幼梗的中部，藉两指的弹力将茶叶摘断，采摘时间以中午十二时至下午三时"},{"text":"前较佳，不同的茶采摘部位也不同，有的采一个顶芽和芽旁的第一片叶子叫一心一叶，有的多采一叶叫一心二叶，也有一心三叶。另有单人式、双人式采茶机，可利用机械采茶，既省力且快速。日光萎凋"},{"text":"采摘下来之茶菁须于日光下摊晒，或利用热风使茶菁水分适度蒸散，减少细胞水分含量，降低其活性并除去细胞膜之半透性，而胞细中各化学成分亦得以藉酵素氧化作用引起发酵作用的进行，搅拌后摊平于笳苈上。"},{"text":"炒青"},{"text":"茶青萎凋至适当程度即以高温炒青破坏叶中酵素活性停止发酵的继续进行，并可除去鲜叶中的臭青味，而鲜叶亦因水分的蒸散而便于揉捻。"},{"text":"揉捻"},{"text":"将炒青后之茶叶置入揉捻机内，使其滚动并形成卷曲状，由于受到揉压，因此遂有部分汁液被挤出而粘附于表面，如此在冲泡时便可很容易地溶解于茶汤之中，不同的茶其揉捻程度也不一样。"},{"src":"\\wulongcha\\maoxiecha\\img_20191122105954c03u22.jpg"},{"text":"干燥"},{"text":"干燥是利用干燥机以热风烘干揉捻后之茶叶，使其含水量低于百分之四，利于贮藏运销，通常为了能使内外干燥一致，常采用二次干燥法，先使其达到七、八成干燥，然后取出回潮，再进行第二次的干燥。"},{"text":"紧压"},{"text":"紧压就是把制成的茶蒸软后加压成块状，这样茶就被称为「紧压茶」，除便于运输、贮藏外，蒸、压、放的过程中也会为茶塑造出另一种老成、粗犷的风味。"},{"text":"蒸"},{"text":"使茶再多热受潮。"},{"text":"压"},{"text":"利用茶叶本身的胶质使叶子紧密连结在一起，稳定了往后陈放期间受潮、陈化的速度。"},{"text":"放"},{"text":"继续紧压前一些成分的降解与陈化，使茶质变得更醇厚。紧压茶的陈放年份是决定市价很重要的因素，当然陈放的品质也很重要紧压的形状有圆饼状、有方砖形、有碗状、有球状、有柱状...等，紧结程度也所不同，有些紧压茶只要用手一剥就可以剥开，有些紧压茶就非得用工具不可。紧结的程度也会影响陈放的效果，紧结程度高者，陈放的效应慢，茶性显得结实，紧结程度低者，陈放的效应快，茶性显得豪放。精制茶青经过萎凋、发酵、杀青、揉捻、干燥等制造工序(不发酵茶略前二项，后发酵茶在揉捻后增加渥堆)后制成茶称为「初制茶」，这样的茶品质并不稳定，不能就此推出市面，否则放一段时间后容易变质，这变质不是说喝了会坏肚子，而是其欣赏价值会降低。初制茶必须再经过精制的过程，茶才算完全制成。"}]},{"name":"毛蟹茶储藏方法","content":[{"text":"1、低温"},{"text":"实验结果表明，温度每升高10℃ ，茶叶的褐变速度要增加3-5倍。在-10℃以下，可以抑制褐变;在-20℃以下贮存几乎能完全防止变质，有条件用冷藏则为良策。一般大型茶企在门店中均会采用此种方法保存茶叶。"},{"text":"2、干燥"},{"text":"毛蟹茶叶在无防潮包装或防潮环境不佳的情况下，周围环境相对湿度愈大则茶叶含水量愈易升高，发茶叶含水量达5%以上，则变质明显，故贮存的环境条件应干燥，最好相对温度能控制在20%以下，使茶叶含水量始终于6%，相对湿度若能控制得更低还要好。另外，茶叶本身含水量在贮存前应进行测定，如果过高，应进行复火，或用生石灰吸湿，甚至更换数次，使含水量控制拓6%以下，即干茶用的指一研便成粉末时，再行贮存。"},{"text":"3、无氧气"},{"text":"空气中约含21%氧气。因茶叶中的许多物质会自动氧化，故无氧条件就能杜绝此类变化。现用真空包装或抽气充氮包装就是根据这一原理。"},{"text":"4、不透光"},{"text":"光线能促进植物色素及类脂物质氧化，即使在低温及无氧条件下，一旦受到强光照射，仍会使茶叶色泽劣变，且产生一种日晒气。故包装材料不宜用透明袋，勿将茶叶放置在光线照射处。许多小包装用防潮袋包装后，外加一厚纸盒，即有防光照作用。有的认为看不见茶叶，特开一口，虽一定程度上增加了美观，但对品质保证而言却是不利的。"},{"text":"5、无异味"},{"text":"毛蟹茶是吸附力很强的物品，切勿与他物放置一起，贮存容器场所均需无异味，否则茶叶会完全变质。"}]},{"name":"毛蟹茶冲泡方法","content":[{"text":"茶具\n        可用盖碗或\n        紫砂壶\n        ，取干茶7克左右，用100度的沸水冲泡。第一泡一般为洗茶，不饮用，加水后立即倒掉。而后几泡时间随个人口味而定，一般能冲泡7、8次以上;其中以2、3、4泡香气最佳;铁观音适宜热饮，冷后一般口感没有热的好喝，并可能会有一点点苦涩味。如在办公室用玻璃杯等品饮时，请按个人口感放置适量茶叶，比正式功夫茶具泡出的口感、香气相对会差一些。"},{"src":"\\wulongcha\\maoxiecha\\img_20191122105954l9rv6d.jpg"}]},{"name":"毛蟹茶功效作用","content":[{"text":"1、兴奋作用："},{"text":"茶叶的咖啡碱能兴奋中枢神经系统，帮助人们振奋精神、增进思维、消除疲劳、提高工作效率。"},{"text":"2、利尿作用："},{"text":"茶叶中的咖啡碱和茶碱具有利尿作用,用于治疗水肿、水滞瘤。利用\n        红茶\n        糖水的解毒、利尿作用能治疗急性黄疸型肝炎。"},{"text":"3、强心解痉作用："},{"text":"咖啡碱具有强心、解痉、松弛平滑肌的功效,能解除支气管痉挛，促进血液循环，是治疗 支气管哮喘、止咳化痰、心肌梗塞的良好辅助药物。"},{"text":"4、抑制动脉硬化作用："},{"text":"茶叶中的茶多酚和维生素C都有活血化瘀防止动脉硬化的作用。所以经常饮茶的人当中,高血压和冠心病的发病率较低。"},{"text":"5、抗菌、抑菌作用："},{"text":"茶中的茶多酚和鞣酸作用于细菌,能凝固细菌的蛋白质,将细菌杀死。可用于治疗肠道疾病，如霍乱、伤寒、痢疾、肠炎等。皮肤生疮、溃烂流脓，外伤破了皮，用浓茶冲洗患处,有消炎杀菌作用。口腔发炎、溃烂、咽喉肿痛,用茶叶来治疗，也有一定疗效。"},{"text":"6、减肥作用："},{"text":"茶中的咖啡碱、肌醇、叶酸、泛酸和芳香类物质等多种化合物，能调节脂肪代谢，特别是\n        乌龙茶\n        对蛋白质和脂肪有很好的分解作用。茶多酚和维生素C能降低胆固醇和血脂,所以饮茶能减肥。"},{"text":"7、防龋齿作用："},{"text":"茶中含有氟，氟离子与牙齿的钙质有很大的亲和力，能变成一种较为难溶于酸的“氟磷灰石”，就象给牙齿加上一个保护层，提高了牙齿防酸抗龋能力。"},{"text":"8、抑制癌细胞作用："},{"text":"据报道，茶叶中的黄酮类物质有不同程度的体外抗癌作用，作用较强的有牡荆碱、桑色素和儿茶素。"}]},{"name":"毛蟹茶鉴别好茶","content":[{"text":"看汤色"},{"text":"取3-5克放入壶中，用沸水冲泡，把泡好的茶汤倒入水晶玻璃杯内观赏汤色。好的的汤色红浓通透明亮，茶的汤色越红品质越好。三年内的较浑浊，十多年以上则红。茶汤泛青、泛黄为陈期不足，茶汤褐黑、浑浊不清、有悬浮物的则是变质的。"},{"text":"看叶底(即茶碴)。"},{"text":"开汤后看冲泡后的叶底(茶渣)，主要看柔软度、色泽、匀度。叶质柔软、肥嫩、有弹性，色泽褐红、均匀一致的好。若叶底无弹性、花杂不匀、发黑，或腐烂如泥、叶张不开展实属品质不好。"}]}]}